html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-inter);
  margin: 0;
  padding: 0;
  background: var(--bg);
  color: var(--text-color);
}

body.dark {
  --bg: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--Light-bg);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-start),
    var(--toggle-bg-end)
  );
  --toggle-btn: var(--dark-bg);
}

body.light {
  --bg: var(--Light-bg);
  --text-color: var(--Light-text2);
  --text-color2: var(--Light-text1);
  --card-bg: var(--Light-card);
  --card-hover: var(--Light-card-hover);
  --toggle: var(--text-color2);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-btn: var(--light-btn);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 40em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --Limegreen: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 28%, 32%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --Light-bg: hsl(0, 0%, 100%);
  --Light-top-bg: hsl(225, 100%, 98%);
  --Light-card: hsl(227, 47%, 96%);
  --Light-card-hover: hsl(228, 33%, 91%);
  --Light-text1: hsl(228, 12%, 44%);
  --Light-text2: hsl(230, 17%, 14%);
  --light-btn: hsl(228, 46%, 96%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --bg: var(--Light-bg);
  --text-color: var(--Light-text2);
  --text-color2: var(--Light-text1);
  --card-bg: var(--Light-card);
  --card-hover: var(--Light-card-hover);
  --toggle: var(--text-color2);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-btn: var(--light-btn);
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg: var(--dark-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text1);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--Light-bg);
    --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-start),
      var(--toggle-bg-end)
    );
    --toggle-btn: var(--dark-bg);
  }
}
:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
  flex-direction: column;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
    flex-direction: row;
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background: var(--card-bg);
  transition: background 150ms ease-in-out;
  color: var(--text-color2);
  text-align: center;
  padding: 1.5625rem;
  cursor: pointer;
  border-radius: 0.3125rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  content: "";
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-start));
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  height: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__count {
  color: var(--text-color);
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
  font-weight: 700;
}
.card__count--small {
  font-size: 2rem;
  font-weight: 700;
}
.card__folowers {
  margin-bottom: 1.5625rem;
}
.card__label {
  color: var(--text-color2);
  font-size: 0.75rem;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--Limegreen);
}
.card__change--down {
  color: var(--bright-red);
}
.card__number {
  margin-left: 0.25rem;
}
.card:hover {
  background: var(--card-hover);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  margin: 0%;
  border: none;
  color: var(--toggle);
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0%;
  margin: 0 -0.125rem;
  height: 1.5rem;
  width: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
  margin-right: 0.8125rem;
}
.toggle label[for=dark] {
  line-height: 1.5rem;
}
.toggle__background {
  position: absolute;
  height: 1.5rem;
  width: 100%;
  top: 0;
  display: block;
  border-radius: 0.75rem;
  background: var(--toggle-bg);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-btn);
  top: 0.1875rem;
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  right: 3px;
  left: calc(100% - 21px);
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
  color: var(--text-color);
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
  margin-top: 0.1875rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  row-gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid img,
.card-grid .card__change {
  justify-self: end;
}